@charset "UTF-8";
@import "../../../style/theme/default/var.css";
.template-page {
  padding: var(--tab-padding-middle);
}
@component-namespace tmp{
  @b panel {
    background-color: var(--color-white);
    margin-bottom: var(--theme-margin-base);
    border: 1px solid var(--theme-border-color-primary);
    @e title {
      height:30px;
      line-height:30px;
      padding-right: var(--theme-padding-base);
      font-size: var(--theme-font-size-base);
    & > span {
          display: inline-block;
          padding: 0 var(--theme-padding-middle);
          border-top: 2px solid var(--theme-color-primary)
        }
      .item{
        font-size: 18px;
      }

    }
    @e body {
      height: 310px;
      padding:var(--theme-padding-small) var(--theme-padding-base);
      &  .echarts{
        width:100%;
        height:100%;
      }
      /* 会员触达方式分析 */
      & .touchmode-lg.echarts{
        height: 180px;
      }
     &  .touchmode-sm.echarts{
        height: 70px;
      }
     &  .touchmode-item{
      display: inline-block;
      position: relative;
      font-size: var(--theme-font-size-small);
      text-align:left;
      padding-left:28px;
      & i{
          position: absolute;
         /*  top:-48px;
          left: 50%;*/
          left:0;
          top:50%;
          margin-top:-16px;
          font-size:23px;
          color: var(--theme-border-color-primary);
        }
        }
      /* /end会员触达方式分析 */
    /* 会员性别分析 */
    .sexy-list{
      margin-top: 60px;
    }
    .sexy-item {
        &.female{
          color: var(--color-danger);
         }
        &.male{
           color: var(--color-primary);
         }
        &.unknown{
           color: var(--theme-border-color-primary);
        }
      }
      .sexy-des p{
        font-size: 10px;
        margin:0;
        padding-top: var(--theme-padding-small);
      }
      .sexy-icon i{
        font-size: 80px;
        display: inline-block;
        padding:var(--theme-padding-base) var(--theme-padding-middle);
        border: 1px solid var(--theme-border-color-primary);
      }

    /* /end 会员性别分析 */
    }
    @e options{
      & a{
        cursor: pointer;
        display: inline-block;
        margin-left: 2px;
      }
    }
  }
}



